/* c. Default and Basic Styles
       Adapted from:
       Skeleton CSS Framework - http://www.getskeleton.com/
       Typeplate Typographic Starter Kit - http://typeplate.com/
 /* ------------------------------------------------------------------ */

/*  1. Basic  ------------------------------------------------------- */

* {
  -moz-box-sizing: border-box;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;

  &:before,
  &:after {
    -moz-box-sizing: border-box;
    -webkit-box-sizing: border-box;
    box-sizing: border-box;
  }
}

html {
  font-size: 62.5%;
  -webkit-font-smoothing: antialiased;
}

body {
  background: #fff;
  font-family: "opensans-regular", sans-serif;
  font-weight: normal;
  font-size: 15px;
  line-height: 30px;
  color: #838c95;
  -webkit-font-smoothing: antialiased;
  /* Fix for webkit rendering */
  -webkit-text-size-adjust: 100%;
}

/*  2. Typography
        Vertical rhythm with leading derived from 6
 --------------------------------------------------------------------- */

h1,
h2,
h3,
h4,
h5,
h6 {
  color: #313131;
  font-family: "opensans-bold", sans-serif;
  font-weight: normal;
}

h1 a,
h2 a,
h3 a,
h4 a,
h5 a,
h6 a {
  font-weight: inherit;
}

h1 {
  font-size: 38px;
  line-height: 42px;
  margin-bottom: 12px;
  letter-spacing: -1px;
}

h2 {
  font-size: 28px;
  line-height: 36px;
  margin-bottom: 6px;
}

h3 {
  font-size: 22px;
  line-height: 30px;
  margin-bottom: 12px;
}

h4 {
  font-size: 20px;
  line-height: 30px;
  margin-bottom: 6px;
}

h5 {
  font-size: 18px;
  line-height: 30px;
}

h6 {
  font-size: 14px;
  line-height: 30px;
}

p {
  margin: 0 0 30px 0;

  img {
    margin: 0;
  }

  &.lead {
    font: 19px/36px "opensans-light", sans-serif;
    margin-bottom: 18px;
  }
}

/* for 'em' and 'strong' tags, font-size and line-height should be same with
 the body tag due to rendering problems in some browsers */

strong,
b {
  font: 15px/30px "opensans-bold", sans-serif;
}

small {
  font-size: 11px;
  line-height: inherit;
}

/*	Blockquotes */

blockquote {
  margin: 30px 0px;
  padding-left: 40px;
  position: relative;

  &:before {
    content: "\201C";
    opacity: 0.45;
    font-size: 80px;
    line-height: 0px;
    margin: 0;
    font-family: arial, sans-serif;
    position: absolute;
    top: 30px;
    left: 0;
  }

  p {
    font-family: "librebaskerville-italic", serif;
    padding: 0;
    font-size: 18px;
    line-height: 36px;
  }

  cite {
    display: block;
    font-size: 12px;
    font-style: normal;
    line-height: 18px;

    &:before {
      content: "\2014 \0020";
    }

    a {
      color: #8b9798;
      border: none;

      &:visited {
        color: #8b9798;
        border: none;
      }
    }
  }
}

/* ---------------------------------------------------------------------
 /*  Pull Quotes Markup
 /*
     <aside class="pull-quote">
       <blockquote>
          <p></p>
       </blockquote>
     </aside>
 /*
 /* --------------------------------------------------------------------- */

.pull-quote {
  position: relative;
  padding: 18px 30px 18px 0px;

  &:before,
  &:after {
    height: 1em;
    opacity: 0.45;
    position: absolute;
    font-size: 80px;
    font-family: Arial, Sans-Serif;
  }

  &:before {
    content: "\201C";
    top: 33px;
    left: 0;
  }

  &:after {
    content: "\201D";
    bottom: -33px;
    right: 0;
  }

  blockquote {
    margin: 0;

    &:before {
      content: none;
    }
  }
}

/* Abbreviations */

abbr {
  font-family: "opensans-bold", sans-serif;
  font-variant: small-caps;
  text-transform: lowercase;
  letter-spacing: 0.5px;
  color: gray;

  &:hover {
    cursor: help;
  }
}

/* drop cap */

.drop-cap:first-letter {
  float: left;
  margin: 0;
  padding: 14px 6px 0 0;
  font-size: 84px;
  font-family: "opensans-bold", sans-serif;
  line-height: 60px;
  text-indent: 0;
  background: transparent;
  color: inherit;
}

hr {
  border: solid #e3e3e3;
  border-width: 1px 0 0;
  clear: both;
  margin: 11px 0 30px;
  height: 0;
}

/*  3. Links  ------------------------------------------------------- */

a {
  text-decoration: none;
  outline: 0;
  color: #11abb0;
  -webkit-transition: color 0.3s ease-in-out;
  -moz-transition: color 0.3s ease-in-out;
  -o-transition: color 0.3s ease-in-out;
  transition: color 0.3s ease-in-out;

  &:visited {
    text-decoration: none;
    outline: 0;
    color: #11abb0;
    -webkit-transition: color 0.3s ease-in-out;
    -moz-transition: color 0.3s ease-in-out;
    -o-transition: color 0.3s ease-in-out;
    transition: color 0.3s ease-in-out;
  }

  &:hover,
  &:focus {
    color: #313131;
  }
}

p a {
  line-height: inherit;

  &:visited {
    line-height: inherit;
  }
}

/*  4. List  --------------------------------------------------------- */

ul,
ol {
  margin-bottom: 24px;
  margin-top: 12px;
}

ul {
  list-style: none outside;
}

ol {
  list-style: decimal;
  margin-left: 30px;
}

ul {
  &.square,
  &.circle,
  &.disc {
    margin-left: 30px;
  }

  &.square {
    list-style: square outside;
  }

  &.circle {
    list-style: circle outside;
  }

  &.disc {
    list-style: disc outside;
  }

  ul,
  ol {
    margin: 6px 0 6px 30px;
  }
}

ol {
  ol,
  ul {
    margin: 6px 0 6px 30px;
  }
}

ul {
  ul li,
  ol li {
    margin-bottom: 6px;
  }
}

ol {
  ol li,
  ul li {
    margin-bottom: 6px;
  }
}

li {
  line-height: 18px;
  margin-bottom: 12px;
}

ul.large li,
li p {
}

/* ---------------------------------------------------------------------
 /*  Stats Tab Markup
 
     <ul class="stats-tabs">
       <li><a href="#">[value]<b>[name]</b></a></li>
     </ul>
 
     Extend this object into your markup.
 /*
 /* --------------------------------------------------------------------- */

.stats-tabs {
  padding: 0;
  margin: 24px 0;

  li {
    display: inline-block;
    margin: 0 10px 18px 0;
    padding: 0 10px 0 0;
    border-right: 1px solid #ccc;

    &:last-child {
      margin: 0;
      padding: 0;
      border: none;
    }

    a {
      display: inline-block;
      font-size: 22px;
      font-family: "opensans-bold", sans-serif;
      border: none;
      color: #313131;

      &:hover {
        color: #11abb0;
      }

      b {
        display: block;
        margin: 6px 0 0 0;
        font-size: 13px;
        font-family: "opensans-regular", sans-serif;
        color: #8b9798;
      }
    }
  }
}

/* definition list */

dl {
  margin: 12px 0;
}

dt {
  margin: 0;
  color: #11abb0;
}

dd {
  margin: 0 0 0 20px;
}

/* Lining Definition Style Markup */

.lining {
  dt,
  dd {
    display: inline;
    margin: 0;
  }

  dt + dt:before {
    content: "\A";
    white-space: pre;
  }

  dd {
    + {
      dt:before {
        content: "\A";
        white-space: pre;
      }

      dd:before {
        content: ", ";
      }
    }

    &:before {
      content: ": ";
      margin-left: -0.2em;
    }
  }
}

/* Dictionary Definition Style Markup */

.dictionary-style {
  dt {
    display: inline;
    counter-reset: definitions;

    + dt:before {
      content: ", ";
      margin-left: -0.2em;
    }
  }

  dd {
    display: block;
    counter-increment: definitions;

    &:before {
      content: counter(definitions, decimal) ". ";
    }
  }
}

/* Pagination */

.pagination {
  margin: 36px auto;
  text-align: center;

  ul li {
    display: inline-block;
    margin: 0;
    padding: 0;
  }

  .page-numbers {
    font: 15px/18px "opensans-bold", sans-serif;
    display: inline-block;
    padding: 6px 10px;
    margin-right: 3px;
    margin-bottom: 6px;
    color: #6e757c;
    background-color: #e6e8eb;
    -webkit-transition: all 200ms ease-in-out;
    -moz-transition: all 200ms ease-in-out;
    -o-transition: all 200ms ease-in-out;
    -ms-transition: all 200ms ease-in-out;
    transition: all 200ms ease-in-out;
    -moz-border-radius: 3px;
    -webkit-border-radius: 3px;
    -khtml-border-radius: 3px;
    border-radius: 3px;

    &:hover {
      background: #838a91;
      color: #fff;
    }
  }

  .current {
    background-color: #11abb0;
    color: #fff;

    &:hover {
      background-color: #11abb0;
      color: #fff;
    }
  }

  .inactive {
    background-color: #e6e8eb;
    color: #a9adb2;

    &:hover {
      background-color: #e6e8eb;
      color: #a9adb2;
    }
  }

  .prev,
  .next {
  }
}

/*  5. Images  --------------------------------------------------------- */

img {
  max-width: 100%;
  height: auto;

  &.pull-right {
    margin: 12px 0px 0px 18px;
