/*  7. Forms  --------------------------------------------------------- */

form,
fieldset {
  margin-bottom: 24px;
}

input {
  &[type="text"],
  &[type="password"],
  &[type="email"] {
    display: block;
    padding: 18px 15px;
    margin: 0 0 24px 0;
    border: 0;
