/* default
 --------------------------------------------------------------- */

.row {
  width: 96%;
  max-width: 1020px;
  margin: 0 auto;
}

/* fixed width for IE8 */

.ie .row {
  width: 1000px;
}

.narrow .row {
  max-width: 980px;
}

.row {
  .row {
    width: auto;
    max-width: none;
    margin: 0 -20px;
  }

  &:before {
    content: " ";
    display: table;
  }

  &:after {
    content: " ";
    display: table;
    clear: both;
  }
}

/* row clearing */

.column,
.columns {
  position: relative;
  padding: 0 20px;
  min-height: 1px;
  float: left;
}

.column.centered,
.columns.centered {
  float: none;
  margin: 0 auto;
}

/* removed gutters */

.row.collapsed > {
  .column,
  .columns {
    padding: 0;
  }
}

.column.collapsed,
.columns.collapsed {
  padding: 0;
}

[class*="column"] + [class*="column"] {
  &:last-child,
  &.end {
    float: right;
  }
}

