/* block grids
 --------------------------------------------------------------------- */

.bgrid-sixths [class*="column"] {
  width: 16.66667%;
}

.bgrid-quarters [class*="column"] {
  width: 25%;
}

.bgrid-thirds [class*="column"] {
  width: 33.33333%;
}

.bgrid-halves [class*="column"] {
  width: 50%;
}

[class*="bgrid"] [class*="column"] + [class*="column"]:last-child {
  float: left;
}

/* Left clearing for block grid columns - columns that changes width in
 different screen sizes. Allows columns with different heights to align
 properly.
 --------------------------------------------------------------------- */

.first {
  clear: left;
}

/* first column in default screen */

.s-first {
  clear: none;
}

/* first column in smaller screens */

/* smaller screens
 --------------------------------------------------------------- */

@media only screen and (max-width: 900px) {
  /* block grids on small screens */
  .s-bgrid-sixths [class*="column"] {
    width: 16.66667%;
  }

  .s-bgrid-quarters [class*="column"] {
    width: 25%;
  }

  .s-bgrid-thirds [class*="column"] {
    width: 33.33333%;
  }

  .s-bgrid-halves [class*="column"] {
    width: 50%;
  }

  /* block grids left clearing */
  .first {
    clear: none;
  }

  .s-first {
    clear: left;
  }
}

/* mobile wide/smaller tablets
 --------------------------------------------------------------- */

@media only screen and (max-width: 767px) {
  .row {
    width: 460px;
    margin: 0 auto;
    padding: 0;
  }

  .column,
  .columns {
    width: auto !important;
    float: none;
    margin-left: 0;
    margin-right: 0;
    padding: 0 30px;
  }

  .row .row {
    width: auto;
    max-width: none;
    margin: 0 -30px;
  }

  [class*="column"] + [class*="column"]:last-child,
  [class*="bgrid"] [class*="column"] + [class*="column"]:last-child {
    float: none;
  }

  /* Offsets */
  .row {
    .offset-1,
    .offset-2,
    .offset-3,
    .offset-4,
    .offset-5,
    .offset-6,
    .offset-7,
    .offset-8,
    .offset-9,
    .offset-10,
    .offset-11 {
      margin-left: 0%;
    }
  }
}

/* mobile narrow
 --------------------------------------------------------------- */

@media only screen and (max-width: 460px) {
  .row {
    width: auto;
  }
}

/* larger screens
 --------------------------------------------------------------- */

@media screen and (min-width: 1200px) {
  .wide .row {
    max-width: 1180px;
  }
}

