}

/* Flex Slider
/* ------------------------------------------------------------------ */

/* Reset */

.flexslider a {
  &:active,
  &:focus {
    outline: none;
  }
}

/* Necessary Styles */

.flexslider {
  position: relative;
  zoom: 1;
  margin: 0;
  padding: 0;

  .slides {
    zoom: 1;

    > li {
      position: relative;
      display: none;
      backface-visibility: hidden;
    }
  }
}

/* Hide the slides before the JS is loaded. Avoids image jumping */

/* Suggested container for slide animation setups. Can replace this with your own */

.flex-container {
  zoom: 1;
  position: relative;
  width: 82%;
}

/* Clearfix for .slides */

.slides {
  &:before {
    content: " ";
    display: table;
  }

  &:after {
    content: " ";
    display: table;
    clear: both;
  }
}

/* No JavaScript Fallback */

/* If you are not using another script, such as Modernizr, make sure you
 * include js that eliminates this class on page load */

.no-js .slides > li:first-child {
  display: block;
}

/* Slider Styles */

.slides {
  zoom: 1;

  > li {
    /*margin-right: 5px; */
    overflow: hidden;
  }
}

/* ------------------------------------------------------------------ */

#image-loader {
  display: none;
  position: relative;
  left: 18px;
  top: 17px;
}

